* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  font-family: "Inter";
}
#root {
  position: relative;
  top: 10%;
}
.game-container {
  padding-bottom: 50px;
}
.status-h {
  font-size: 30pt;
  padding-bottom: 50px;
  text-align: center;
  font-weight: 300;
}
.game-board {
  display: block;
  width: fit-content;
  margin: auto;
}
.game-row:after {
  clear: both;
  content: "";
  display: table;
}
.cell {
  border: 1px solid #999;
  text-align: center;
  float: left;
  height: 100px;
  line-height: 100px;
  font-size: 50px;
  width: 100px;
  margin-right: -1px;
  margin-top: -1px;
}
.history-board .cell {
  width: 34px;
  line-height: 34px;
  height: 34px;
  font-size: 24px;
}
.history-container button {
  pointer-events: none;
}
.history-board {
  display: flex;
  flex-flow: row wrap;
}

.history-board {
  display: inline-block;
  margin: 30px;
  opacity: 70%;
}
.history-board:hover {
  opacity: 100%;
}
.history-label {
  font-weight: 300;
  margin-left: 50px;
}
